.Pokecard{
    width:250px;
    text-align:center;
    padding:10px;
    margin: 1rem;
    box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56);
    background-color: white;
    border-radius: 5px;
}

.Pokecard-data{
    font-size: 80%;
}

.Pokecard-image{
    display:flex;
    justify-content: center;
    align-items: center;
    height: 250px;
    background-color: #eceff1;
    border-radius: 5px;
    margin: 1rem;

}
.Pokecard-title{
    margin-bottom:1rem ;
    font-weight: bold;
    font-size: 1.5rem;
}
.Pokecard img{
    transition: all ease 500ms;
}
.Pokecard img:hover{
    transform:scale(1.8);
    opacity: 0.7;

}